import React from 'react';
import Slider from "react-slick";
import CardSlide from '../UI/CardSlide/CardSlide';
import './OurNanny.scss';


function OurNanny(props) {

    const nanniesData = [
        {
            name: 'Бондар Анастасія',
            description: 'Закінчила курси медичної допомоги',
            imgUrl: '/assets/images/photoNannies/nanny1.png'
        },
        {
            name: 'Коваль Оксана',
            description: 'Має педагогічну освіту',
            imgUrl: '/assets/images/photoNannies/nanny4.png'
        },
        {
            name: 'Іванова Юлія',
            description: 'Досвід роботи нянею - 5 років',
            imgUrl: '/assets/images/photoNannies/nanny2.png'
        },
        {
            name: 'Семенова Тетяна',
            description: 'Сертифікований масажист',
            imgUrl: '/assets/images/photoNannies/nanny3.png'
        },

    ]
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 4,
        autoplay: true,

        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />,
        responsive: [
            {
                breakpoint: 1203,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                }
            },
            {
                breakpoint: 887,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                }
            },
            {
                breakpoint: 617,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };

    function SampleNextArrow(props) {
        const { onClick } = props;
        return (
            <div
                className='arrow-next arrow-next-nannies'
                onClick={onClick}
            >
                <img src="assets/icons/arrow_right_icon.svg" />
            </div>
        );
    }

    function SamplePrevArrow(props) {
        const { onClick } = props;
        return (
            <div
                className='arrow-prev arrow-prev-nannies'
                onClick={onClick}
            >
                <img src="assets/icons/arrow_left_icon.svg" />
            </div>
        );
    }

    return (
        <section className='our-nanny' id='our-nannies'>
            <div className='our-nanny-body container'>
                <div className='our-nanny-header'>
                    <h2><span>Наші<img src='/assets/otherIcons/draw_lines.svg' /></span> няні</h2>
                    <p>Перед початком роботи всі няні сервісу проходять навчання незалежно від досвіду та освіти.</p>
                </div>
            </div>
            <div className='container' style={{ marginBottom: '70px' }}>
                <Slider  {...settings}>
                    {nanniesData.map((nanny, i) => {

                        return <div key={i}>
                            <div className="slide-container" style={{ display: 'flex', justifyContent: 'center' }}>
                                <CardSlide nanny={nanny} />
                            </div>
                        </div>
                    })}
                </Slider>
            </div>
        </section >
    );
}

export default OurNanny;
