import React from 'react';
import classes from './Button.module.scss';


function Button({ children, ...props }) {
    return (
        <button type={props.type ? props.type : ''} style={{ ...props.style }} className={props.typeColor === 'primary1'
            ? classes.buttonPrimary1
            : props.typeColor === 'primary2'
                ? classes.buttonPrimary2
                : props.typeColor === 'secondary' ?
                    classes.buttonSecondary : null

        }

        // onClick={() => props.setActive(true)}
        >
            {children}
        </button>
    );
}

export default Button;
