import React from 'react';
import Button from '../Button/Button';
import classes from './Card.module.scss';

function Card(props) {
    return (
        <div className={props.typeColor === 'primary1'
            ? classes.cardPrimary1
            : props.typeColor === 'primary2'
                ? classes.cardPrimary2
                : props.typeColor === 'secondary'
                    ? classes.cardSecondary
                    : null}>
            <img src={props.urlImg} className={classes.cardImg} />
            <h3 className={classes.title}>{props.title}</h3>
            <ul className={classes.list}>
                {props.items.map((e, i) => {
                    return <li key={i} className={classes.listItem}>{e}</li>
                })}
            </ul>
            <Button typeColor={props.typeColor}>Знайти няню</Button>
        </div >
    );
}

export default Card;
